import "flowbite";
import { Geist, Geist_Mono } from "next/font/google";
import "@/app/globals.css";
import { Toaster } from "react-hot-toast";
import MetadataManager from "@/components/MetadataManager";

const geistSans = Geist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono",
  subsets: ["latin"],
});

// Hàm lấy thông tin cài đặt từ localStorage
const getWebsiteSettings = () => {
  if (typeof window !== 'undefined') {
    try {
      const settings = localStorage.getItem('websiteSettings');
      return settings ? JSON.parse(settings) : null;
    } catch (error) {
      console.error('Lỗi khi đọc settings:', error);
      return null;
    }
  }
  return null;
};

export const metadata = {
  title: "Điện Máy Quốc Bảo",
  description: "Cửa hàng điện máy uy tín, chất lượng cao",
};

export default function RootLayout({ children }) {
  return (
    <html lang="vi">
      <body className={`${geistSans.variable} ${geistMono.variable}`}>
        <MetadataManager />
        <Toaster />
        {children}
      </body>
    </html>
  );
}
